@import url(https://fonts.googleapis.com/css?family=Lato:400,700&subset=latin-ext);
* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif; }

.main {
  background: url(/Weather-API-React/static/media/background.60d77e97.jpg) no-repeat center;
  background-size: cover;
  min-height: 100vh;
  overflow: hidden; }
  .main .container {
    max-width: 80vw;
    margin: 0 auto;
    text-align: center; }

.input__header {
  font-size: 3rem;
  color: #222831; }
  .input__header::after {
    content: '';
    border: 1px solid #222831;
    width: 40%;
    display: block;
    margin: 10px auto; }

.input__text {
  max-width: 70%;
  font-size: 1.2rem;
  vertical-align: bottom;
  padding: 10px;
  border-radius: 10px 0 0 10px;
  border: 2px solid #00adb5;
  transition: 0.7s; }
  .input__text:focus {
    outline: none;
    border-color: #222831; }

.input__btn {
  font-size: 1rem;
  letter-spacing: 0.5px;
  padding: 10px;
  height: 46px;
  border: none;
  border-radius: 0 10px 10px 0;
  background: #00adb5;
  color: #fff;
  transition: 0.7s; }
  .input__btn:focus {
    outline: none; }
  .input__btn:hover {
    cursor: pointer;
    background: #222831; }

.icon__wrapper {
  padding-top: 25px; }
  .icon__wrapper .icon__img {
    width: 70px;
    height: 70px; }

.content__wrapper {
  font-size: 24px;
  color: #222831; }
  .content__wrapper .city__info {
    font-weight: bold;
    padding: 10px; }
    .content__wrapper .city__info .city__info--italic {
      color: #20003D;
      font-style: italic; }
  .content__wrapper .weather__info {
    padding: 10px; }
    .content__wrapper .weather__info .weather__info--bg {
      padding: 10px 20px;
      background: #20003D;
      color: #fff;
      border-radius: 20px; }

.weather__info--error {
  font-size: 24px;
  color: #222831; }

